:root {
  --color-light: #eee;
  --color-dark: #1a1a1a;
  --color-accent: #ff3d00;
}

body {
  background-color: var(--color-dark);
  background-image: linear-gradient(var(--color-dark), black);
  min-height: 100vh;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 2px solid #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 12px;
  height: 12px;
  border: 2px solid #fff;
  border-radius: 50%;
  position: absolute;
  top: 4px;
  left: 4px;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.hidden {
  display: none;
}

h1, h2, p {
  margin: 0;
}

.main-heading {
  color: var(--color-accent);
  text-align: center;
  text-transform: uppercase;
  margin: 0 1rem;
  padding-top: 1rem;
  font-size: 3em;
}

.cat-info {
  max-width: 300px;
  margin: 0 auto;
}

.image {
  box-shadow: 0 0 10px var(--color-accent);
  max-width: 300px;
  border-radius: 15px;
  margin: 0 auto;
}

.second-heading {
  color: var(--color-accent);
  text-align: center;
  padding: 1rem 0;
  font-size: 2em;
}

.paragraph {
  color: var(--color-light);
  padding-bottom: .8em;
  font-size: 1.2em;
}

.strong {
  color: var(--color-accent);
}

@media (min-width: 600px) {
  .cat-info, .image {
    max-width: 500px;
  }
}

@media (min-width: 1000px) {
  .cat-info, .image {
    max-width: 800px;
  }
}

